.footer {
  position: relative;
  padding: 0px 20px 140px 20px;
  background-color: #fff;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 104px minmax(400px, 800px) 100px;
      grid-template-columns: 104px minmax(400px, 800px) 100px;
  gap: 4vw;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.footer img {
  width: 100%;
  content: url("../images/footerLogo.webp");
}

.footer .restaurant {
  text-align: left;
}

.footer .restaurant .name {
  font-size: 44px;
  padding-bottom: 0px;
}

.footer .buttons {
  width: 100%;
  height: 100%;
  text-align: left;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.footer .buttons a {
  font-size: 14px;
}

.footer .copy {
  position: absolute;
  width: 100%;
  bottom: 20px;
  margin: auto;
}
