.navbar {
  position: fixed;
  z-index: 11;
  width: 100%;
  height: 68px;
  -webkit-box-shadow: 0px 1px 8px 1px #080808;
          box-shadow: 0px 1px 8px 1px #080808;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.navbar::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  background-color: #000;
  opacity: 0.6;
}

.navbar .main-logo {
  position: absolute;
  left: 20px;
  top: 5px;
  background-image: url("../images/navbarLogo.webp");
  height: 60px;
  width: 100px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.navbar .menu {
  position: absolute;
  height: 100%;
  right: 20px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 40px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: #ccc;
  font-weight: bold;
}
