* {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

@font-face {
  font-family: 'Italianno';
  src: local("Italiano"), url("../Fonts/Italianno.ttf") format("truetype");
}

@font-face {
  font-family: 'Italiana';
  src: local("Italiana"), url("../Fonts/Italiana.ttf") format("truetype");
}

a {
  all: unset;
}

a:hover {
  color: #aa1915;
  cursor: pointer;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.App {
  text-align: center;
  background-color: #1b1b1b;
  width: 100%;
  min-height: 100vh;
}

.red {
  color: #aa1915;
}

.black {
  color: #1b1b1b;
}

.white {
  color: #fff;
}

.grey {
  color: #777;
}

.font1 {
  padding: 12px;
  font-size: 40px;
  font-family: 'Italianno';
}

.font2 {
  padding: 12px;
  padding-bottom: 16px;
  font-size: 72px;
  line-height: 1em;
  font-family: 'Italiana';
}

.font3 {
  padding: 12px;
  font-size: 16px;
}

.font4 {
  padding: 12px;
  font-size: 16px;
}
